<template>
  <div class="container">
    <el-row :gutter="24">
      <el-col :xl="{span: 16, offset: 4}" :lg="{span: 16, offset: 4}" :md="{span: 16, offset: 4}" :sm="{span: 18, offset: 3}" :xs="{span: 24, offset:0}">
        <el-card class="box-card" shadow="never">
          <div slot="header" class="clearfix card-header">
            <span>发表留言</span>
          </div>
          <el-input
            type="textarea"
            :autosize="{ minRows: 2}"
            placeholder="请输入内容"
            v-model="content">
          </el-input>
          <br><br>
          <div class="clearfix">
            <el-button style="float: right; padding: 3px 0" type="text" @click="sendMsg">发送</el-button>
          </div>
          <br>
        </el-card>
        <transition-group name="el-zoom-in-center">
          <el-card class="box-card transition-box" v-for="(msg, index) in msgList" :key="msg.time" :show="msg.show" shadow="never">
            <div class="text item">
              {{ msg.content }}
            </div>
            

            <!-- 评论 -->
            <transition name="fade">
              <div class="reply-container" v-if = "msg.reply">
                <div class="devider"></div>
                <div class="reply-title">管理员回复</div>
                <div class="reply-content" >
                  {{ msg.reply }}
                </div>
              </div>
            </transition>

            <!-- 发表评论 -->

            <!-- <div class="time-block">
              <div class="time">
                <el-button v-if="commentIndex == index" icon="el-icon-arrow-up" size="mini" @click="showReply(index)">收起</el-button>
                <el-button v-if="commentIndex != index" type="primary" icon="el-icon-arrow-down" size="mini" @click="showReply(index)">评论</el-button>
              </div>
            </div>
            <br>
            
            <transition name="fade">
              <div v-if="msg.showReply == true">
                <el-card class="box-card" shadow="never">
                  <el-row :gutter="20">
                    <el-col :xs="16" :sm="16" :md="20" :lg="21">
                      <el-input
                        placeholder="请输入内容"
                        v-model="commentContent">
                      </el-input>
                    </el-col>
                    <el-col :xs="3" :sm="2" :md="2">
                      <el-button type="primary" icon="el-icon-edit" size="medium" @click="reply(msg)">评论</el-button>
                    </el-col>
                  </el-row>
                </el-card>
              </div>
            </transition> -->
          </el-card>
        </transition-group>
      </el-col>
    </el-row>
  </div>
</template>
<style lang="scss">
  html {
    background-color: #F6F6F6;
  }
  .container {
      font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  }
  .box-card { 
    margin-bottom: 20px;
  }
  .time-block {
    color: #E4E7ED;
    font-size: 13px;
  }
  .reply-container {
    // border: 1px solid red;
    padding: 10px;
  }
  .reply-title {
    color: #AAA;
    font-weight: 100;
  }
  .reply-content {
    word-wrap:break-word;
    word-break:break-all;
    margin-top: 6px;
  }
  .devider {
    border-top: 1px solid #EEE;
    margin-bottom: 10px;
  }
</style>
<script>
  // import MobileMenu from './MobileMenu.vue'
  export default {
    data() {
      return {
        msgList: [], //留言列表
        content: '', //发表内容
        commentIndex: -1,
        commentContent: '', //评论内容
      }
    },
    components: {
    },
    methods: {
      getList() {
        this.$http.get('https://api.cancercare.net.cn/message')
        .then((res) => {
            // 请求成功回调
            this.msgList = res.body.content.reverse()
        }, () => {
            // 请求失败回调
        });
      },
      showItem() {
        this.msgList = this.msgList.map(item => {
          console.log(item)
          return {
            ...item,
            show: true,
            showReply: true,
          }
        })
      },
      sendMsg() {
        this.$http.post('https://api.cancercare.net.cn/message',{
          content: this.content
        }).then((res) => {
            // 请求成功回调
            if (!res.body.error){
              this.getList()
            }
            else {
              alert(res.body.message)
            }
        }, () => {
            // 请求失败回调
        });
      },
      showReply(index) {
        console.log(index)
        if (this.commentIndex != -1) {
          this.$set(this.msgList[this.commentIndex], 'showReply', false)
        }
        if (this.commentIndex != index) {
          this.$set(this.msgList[index], 'showReply', true)
          this.commentIndex = index
        }
        else {
          this.commentIndex = -1
        }
      },
      reply(msg) {
        console.log(msg)
        this.$http.post('https://api.cancercare.net.cn/message/reply',{
          id: msg.id,
          reply: this.commentContent
        }).then((res) => {
            // 请求成功回调
            console.log(res.body)
            alert(res.body.message)
            if (!res.body.error){

            }
        }, () => {
            // 请求失败回调
        });
      }

    },
    mounted() {
      this.getList()
      this.showItem()
    }
  }

</script>



